import React, { Component } from 'react'
import '../../assets/css/ml.css'

export default class Feedback extends Component {
  render() {
    const { infos } = this.props
    const disabled =
      infos.firstName.valid &&
      infos.lastName.valid &&
      infos.age.valid &&
      infos.email.valid &&
      infos.skills.valid

    console.log(disabled, 'disabe')

    return (
      <div className=" cspan border-1 ">
        <h1> Feedback </h1>
        <div className="grid-2 border-1">
          <span className="cspan">FirstName</span>
          <span className="nspan">{infos.firstName.message}</span>
          <span className="cspan">LastName</span>
          <span className="nspan">{infos.lastName.message}</span>
          <span className="cspan">Email</span>
          <span className="nspan">{infos.email.message}</span>
          <span className="cspan">Age</span>
          <span className="nspan">{infos.age.message}</span>
          <span className="cspan">Skills</span>
          <span className="nspan">{infos.skills.message}</span>
        </div>
        <div className="btdiv">
          <button
            className=" special boxshadow"
            onClick={() => this.props.prevPage()}
          >
            Edit
          </button>

          <button
            className=" special boxshadow"
            disabled={!disabled}
            onClick={() => this.props.nextPage()}
          >
            Confirm!
          </button>
        </div>
      </div>
    )
  }
}
